#main {
  width: 100vw;
  height: 100vh;
  padding: 16px 8px 16px 16px;
  font-size: 14px;
}
#main .ant-tree {
  width: 100%;
  height: 200px;
  overflow-y: auto;
  padding-top: 8px;
}
#main .ant-tree .ant-tree-switcher {
  width: 0;
  display: none;
}
#main .ant-tree .ant-tree-node-content-wrapper {
  width: 100%;
}
#main .box {
  height: auto;
  border: 1px solid #666;
  margin: 16px 0;
  position: relative;
  padding: 16px 0;
}
#main .box .box-title {
  position: absolute;
  top: -14px;
  left: 20px;
  background-color: #fff;
  padding: 0 5px;
  font-weight: bold;
  font-style: italic;
}
#main .box .box-content {
  display: flex;
  font-size: 14px;
  padding: 0 8px;
}
#main .box .box-content .column {
  width: 50%;
}
#main .box .box-content .column:first-child {
  padding-right: 8px;
}
#main .box .box-content .column:last-child {
  padding-left: 8px;
}
#main .box .box-content .column label {
  width: 70px;
  text-align: right;
  line-height: 32px;
  padding-right: 5px;
}
#main .box .box-content .column label::after {
  content: ":";
}
#main .box .box-content .column .ant-input {
  width: calc(100% - 70px);
}
#main .box .box-content .column .row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
}
#main .box .box-content .ant-radio-group {
  width: 100%;
}
#main .chart-container {
  width: 100%;
  height: 33.33333333%;
  position: relative;
}
#main .chart-container .chart,
#main .chart-container .loading_mask {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
#main .chart-container .loading_mask {
  background: rgba(255, 255, 255, 0.6);
  display: none;
  align-items: center;
  justify-content: center;
}
#main .chart-container:before {
  position: absolute;
  top: -10px;
  width: 100%;
  height: 30px;
  text-align: center;
  line-height: 30px;
  z-index: 999;
  font-weight: bold;
  font-size: 16px;
}
#main .chart-container:first-child:before {
  content: "A->B";
}
#main .chart-container:nth-child(2):before {
  content: "B->A";
}
#main .chart-container:last-child:before {
  content: "SYM";
}
